export const dribble_right = css`
  animation: 1s ease-in-out dribble_right forwards;

  @keyframes dribble_right {
    0% {
      opacity: 0;
      transform: translateX(100px);
    }
    70% {
        transform: translateX(-10px);
    }
    100% {
      opacity: 1;
      transform: translateX(0px);
    }
  }
`;
