export const Arrow = ({ color, width = "24" }) => {
  return (
    <svg width={width} height={width} fill="none" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
      <path
        d="M18 6 6 18M8 6h10v10"
        stroke={color ? color : "#fff"}
        strokeWidth="2"
        strokeLinecap="round"
        strokeLinejoin="round"
      />
    </svg>
  );
};
