import styled from "styled-components";
import { Text } from "../../assets/styles/component";

const SPost = styled.a`
  display: flex;
  flex-direction: column;
  gap: 24px;

  width: 32.6%;

  @media (max-width: 1200px) {
    width: calc(50% - 20px);
  }

  height: 380px;

  position: relative;

  text-align: left;
  cursor: pointer;

  background-color: white;
  transition: 0.25s ease-in-out;

  &::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0px;
    left: 0px;
    z-index: -10;
  }

  &:hover {
    transform: translateX(-2px) translateY(-5px);
  }
`;

const Img = styled.img`
  aspect-ratio: 521/380;
  height: 100%;
  border-radius: 30px;
  aspect-ratio: 380 / 265;
`;

const Desc = styled.div`
  position: absolute;
  bottom: 20px;
  left: 0;

  padding: 19px 21px;
  margin: 0 20px;

  background: rgba(255, 255, 255, 0.21);
  border: 2px solid rgba(238, 247, 255, 0.25);

  backdrop-filter: blur(16.5px);
  border-radius: 23px;

  display: flex;
  flex-direction: column;
  gap: 10px;
`;

const Info = styled.div`
  display: flex;
  gap: 9px;
  align-items: center;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  color: #ffffff;

  opacity: 0.4;
`;

const Line = styled.div`
  width: 1px;
  height: 11px;
  opacity: 0.3;
  background-color: #ffffff;
`;

const SText = styled(Text)`
  font-weight: 400;
  font-size: 20px;
  line-height: 120%;
  color: #ffffff;
`;

const Post = ({ img, desc, href, date, type }) => {
  return (
    <SPost href={href}>
      <Img src={img} alt="post_img" loading="lazy" />
      <Desc>
        <SText color="#3C3C3C" size={20}>
          {desc}
        </SText>
        <Info>
          {date} <Line /> {type === 1 ? "Кейсы" : "Клиенты"}
        </Info>
      </Desc>
    </SPost>
  );
};

export default Post;
