import styled from "styled-components";
import { css } from "styled-components";
import { Arrow } from "../images/Elements/arrow-up-right";

const ButtonStyle = css`
  display: flex;
  justify-content: center;
  align-items: center;

  border: none;
  gap: 16px;

  font-family: "HeliosExtC", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;

  color: ${({ colorTxt = "#ffffff" }) => colorTxt};
  background: transparent;

  cursor: pointer;
`;

const ButtonBlue = styled.button`
  ${ButtonStyle}
  background: linear-gradient(217.29deg, #0e7ed8 10.81%, #04a2e4 92.27%);
  border-radius: 18px;
  padding: 26px 36px;

  &:active {
    box-shadow: inset 0px 0px 15px 5px #0b65af;
  }
`;

const ButtonSecondary = styled.button`
  ${ButtonStyle}
  padding: 26px 36px;
  width: 100%;
  background: ${({ color }) => color};
  border-radius: 60px;

  &:active {
  border-radius: 60px;
    box-shadow: inset 0px 0px 15px 5px ${({ color = "#00000052" }) => color};
  }
`;

const ButtonService = styled.button`
  ${ButtonStyle}

  padding: 25px 51px;
  width: ${({ width = "fit-content" }) => width};
  background: ${({ color }) => color};
  border-radius: 18px;

  &:active {
    box-shadow: inset 0px 0px 15px 5px ${({ color = "#00000052" }) => color};
  }
`;

const ButtonGray = styled.button`
  ${ButtonStyle}
`;

const Button = ({
  type = "blue",
  color,
  colorTxt,
  children,
  width,
  link = false,
  handle,
}) => {
  const renderButton = () => {
    switch (type) {
      case "blue":
        return (
          <ButtonBlue>
            {children} {link ? <Arrow /> : false}
          </ButtonBlue>
        );
      case "secondary":
        return (
          <ButtonSecondary
            color={color ? color : "#ffffff"}
            colorTxt={colorTxt}
          >
            {children} {link ? <Arrow color={colorTxt} /> : false}
          </ButtonSecondary>
        );
      case "gray":
        return (
          <ButtonGray>
            {children} {link ? <Arrow /> : false}
          </ButtonGray>
        );
      case "service":
        return (
          <ButtonService
            width={width}
            color={color ? color : "#ffffff22"}
            colorTxt={colorTxt}
          >
            {children}
            {link ? <Arrow color={colorTxt} /> : false}
          </ButtonService>
        );
    }
  };
  return renderButton();
};

export default Button;
