import styled from "styled-components";
import { useEffect, useRef } from "react";
import { useState } from "react";

import { Text, Title3 } from "../../assets/styles/component";
import Button from "../../assets/UI/ButtonDesktop";
import Post from "./postDesktop";

import post_img_1 from "../../assets/images/Test/post_img_1.png";
import post_img_2 from "../../assets/images/Test/post_img_2.png";
import post_img_3 from "../../assets/images/Test/post_img_3.png";

import { EngLoc } from "../../assets/constants/localizations";
import { smooth_up } from "../../assets/styles/animations";
import { Telegram } from "../../assets/images/Socials/Telegram";
import { YandexZen } from "../../assets/images/Socials/YandexZen";

const SPosts = styled.section`
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 0 8.3%;
  padding: 190px 0;
  gap: 20px;

  @media (max-width: 1400px) {
    margin: 0 5%;
    flex-wrap: wrap;
    justify-content: center;
  }

  & > div:last-child {
    flex-direction: row-reverse;

    @media (max-width: 1200px) {
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: flex-start;
    }
  }
`;

const Title = styled(Title3)`
  font-weight: 400;
  font-size: 96px;
  line-height: 110%;

  margin-bottom: 100px;

  background: linear-gradient(90.45deg, #116ad0 1.65%, #00aee8 114.47%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
`;

const PostsList = styled.div`
  display: flex;
  gap: 20px;
  width: 100%;

  @media (max-width: 1300px) {
    justify-content: flex-start;
  }

  & {
    opacity: 0;
    ${({ view }) => !view && smooth_up}
  }
`;

const Description = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  gap: 37px;

  width: 32.8%;

  @media (max-width: 1200px) {
    width: 42.8%;
  }

  & p {
    max-width: 380px;
    font-weight: 400;
    font-size: 20px;
    line-height: 150%;
    color: #000;
  }

  & button {
    max-height: 72px;
    border-radius: 34.5px;
    border: 1px solid rgba(0, 174, 232, 0.37);
  }

  & > div {
    display: flex;
    gap: 15px;
  }
`;

const Social = styled.div`
  width: 72px;
  height: 72px;

  border: 1px solid rgba(0, 174, 232, 0.37);
  border-radius: 34.5px;

  display: flex;
  justify-content: center;
  align-items: center;
`;

const ListPosts = [
  {
    img: post_img_2,
    desc: "Как продать подписку за 6 шагов: схема прогрева в соцсетях",
    date: "18.10.2022",
    type: 1,
  },
  {
    img: post_img_1,
    desc: "Как продать подписку за 6 шагов: схема прогрева в соцсетях",
    date: "18.10.2022",
    type: 2,
  },
  {
    img: post_img_2,
    desc: "Как продать подписку за 6 шагов: схема прогрева в соцсетях",
    date: "18.10.2022",
    type: 1,
  },
  {
    img: post_img_3,
    desc: "Как продать подписку за 6 шагов: схема прогрева в соцсетях",
    date: "18.10.2022",
    type: 1,
  },
  {
    img: post_img_1,
    desc: "Как продать подписку за 6 шагов: схема прогрева в соцсетях",
    date: "18.10.2022",
    type: 1,
  },
  {
    img: post_img_3,
    desc: "Как продать подписку за 6 шагов: схема прогрева в соцсетях",
    date: "18.10.2022",
    type: 1,
  },
];

const Posts = ({ language }) => {
  const [view, setView] = useState(true);

  const intersectionObserver = new IntersectionObserver((entries) => {
    if (entries[0].intersectionRatio <= 0) return;
    setView(false);
  });

  useEffect(() => {
    intersectionObserver.observe(elem.current && elem.current);
  }, []);

  const elem = useRef(null);

  return (
    <SPosts ref={elem}>
      <Title weight={300}>
        {language === "Ru" ? "Наши статьи" : EngLoc.posts.title}
      </Title>
      <PostsList view={view}>
        {ListPosts.map((post, index) => {
          if (index < 2)
            return (
              <Post
                key={Math.random()}
                type={post.type}
                date={post.date}
                img={post.img}
                desc={post.desc}
              />
            );
          return null;
        })}
      </PostsList>
      <PostsList view={view}>
        {ListPosts.map((post, index) => {
          if (index > 1 && index < 4)
            return (
              <Post
                key={Math.random()}
                type={post.type}
                date={post.date}
                img={post.img}
                desc={post.desc}
              />
            );
          return null;
        })}
        <Description>
          <Text>
            Больше полезных статей о маркетинге в социальных сетях и мессенжерах
            читайте в блоге или в удобной социальной сети
          </Text>
          <div>
            <a href="https://blog.socialjet.pro/">
              <Button
                width="100%"
                type="service"
                colorTxt="#00AEE8"
                color="#fff"
                link={true}
              >
                {language === "Ru" ? "Наш блог" : EngLoc.buttons.blog}
              </Button>
            </a>

            <Social>
              <Telegram color="#00AEE8" />
            </Social>
            <Social>
              <YandexZen color="#00AEE8" />
            </Social>
          </div>
        </Description>
      </PostsList>
    </SPosts>
  );
};

export default Posts;
